.share-panel {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 15px;
  height: 50px;
  align-items: center;

  &__button {
    border: none;
    width: 30px;
    height: 30px;
    background: transparent;
    background-repeat: no-repeat;
    cursor: pointer;
  }

  &__link {
    width: 30px;
    height: 30px;
  }
}
